
.login-banner {
    box-shadow: 0 2px 0px -1px rgba(0, 0, 0, 0.2), 0 0px 0px rgba(0, 0, 0, 0.14), 0 1px 10px rgba(0, 0, 0, 0.12) !important;
    -ms-flex-wrap: nowrap !important;
        flex-wrap: nowrap !important;
}
.login-container {
    background: url('/static/login-screen.gif') no-repeat center center fixed !important;
    background-size: cover !important;
}
.login-container::before {
    background: radial-gradient(transparent 40%, rgba(0,0,0,.3));
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
}
.login-container .justify-center {
    height: 100vh;
    min-height: 460px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.login-input-container {
    margin: 10px 10px 10px 10px;
}
.login-title {
    text-align: center;
}
.loading-pic {
    position: fixed;
    z-index: 10;
    background-color: black;
    background-size: cover;
    -webkit-animation: fadein 0.5s;
            animation: fadein 0.5s;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
@-webkit-keyframes fadein {
from { opacity: 0;
}
to   { opacity: 1;
}
}
@keyframes fadein {
from { opacity: 0;
}
to   { opacity: 1;
}
}



