
.notify_card {
    margin-top: 2px;
    margin-bottom: 2px;
}
.list-complete-enter-active {
    overflow: hidden;
    transition: all 1s;
}
.list-complete-leave-active {
    margin-top: 0px;
    overflow: hidden;
    transition: all 1s;
}
.list-complete-enter, .list-complete-leave-to {
    height: 0px;
    opacity: 0;
    padding: 0px;
    margin-top: 0px;
    overflow: hidden;
}
.homescreen {
    height: calc(100vh - 4rem);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.homescreen .no-event, .homescreen .tips {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.homescreen .tips {
    margin-top: 2rem;
}




