/** CSS Styles */

html {
  overflow-y: auto !important;
}

::-webkit-scrollbar {
  width: 0;
  height: 0;
}

#glimpse .glimpse-container {
  height: calc(100vh - 4rem);
  display: flex;
  align-items: center;
}

#glimpse .card {
  border-radius: 0;
}

#glimpse a {
  color: inherit;
}

#glimpse .list {
  margin: 0 2rem !important;
}

#glimpse main {
  text-align: center;
}

#glimpse h2 {
  text-align: center;
}

/*修正提示*/
.unfucked_read::after {
  background: #f44336 !important;
  width: auto !important;
  height: 15px !important;
  border-radius: 20% !important;
  top: 0px !important;
}

/*去掉下划线*/
a {
  text-decoration: none;
}

.bottom_nav_btn {
  filter: grayscale(0%) !important;
  min-width: 100px !important;
  max-width: 100px !important;
}


.fade-enter-active {
  transition: all .3s ease;
}
.fade-leave-to {
  opacity: .5;
}
.fade-enter {
  opacity: .5;
}

.bottom-nav .btn .icon {
  transition: none !important;
}



